<template>
   <div>
    <!-- <router-link
      tag="button"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
      :to="{ name: 'rawMaterials.createView' }"
    >
      <i class="material-icons">add</i> Add
    </router-link> -->
    <div class="mdl-layout-spacer"></div>

    <data-table
      :columns="columns"
      :data="rawmaterials.data"
      :actionMethod="fetchRawMaterials"
      :count="rawmaterials.count"
      :searchPlaceholder="'Search by Name'"
    ></data-table>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
export default {
  name: 'rawmaterials-list-view',
  components: {
    DataTable
  },
  computed: {
    ...mapGetters({
      rawmaterials: 'getRawmaterials'
    })
  },
  methods: {
    ...mapActions([
      'fetchRawMaterials'
    ])
  },
  data () {
    return {
      columns: [
        {
          field: 'name',
          label: 'Name',
          type: String
        },
        {
          field: 'quantity',
          label: 'Quantity',
          type: String
        },
        {
          field: 'is_active',
          label: 'Active',
          type: Boolean
        }
      ]
    }
  }
}
</script>



